
import api from '../server'
import navigate from '../utils/navigate'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

export default {
components:{
CheckIcon
},
data(){
return {
phone:'',
code:'',
time:'',
canclick:true,
agree:true
}
},
created(){
this.phone = this.$route.params.phone//电话
},
computed:{
canlogin(){
return (this.code.length>0 && this.agree)
}
},
methods:{
getcode(){//获取验证码
var _this = this;
if(this.canclick){
this.canclick = false
}else {
return false;
}
this.time = 20;
var t=setInterval(function () {
if(_this.time>0){
_this.time --;
}else {
clearInterval(t);
_this.canclick = true;
}
},1000);
var data = {
phone:this.phone
};
api.sendcode(data).then(function (res) {
_this.$vux.toast.text('验证码发送成功','top')
})
},
login(){
var _this = this;
if(this.canlogin){
var openid;
if(navigate() == 'wechat'){
openid = _this.$store.state.openid;
}
var data = {
cellPhone:this.phone,
code:this.code,
openId:openid
};
api.login(data).then(function (res) {
if(res.code == '000'){
var data=JSON.parse(res.data);
_this.$store.state.token = localStorage.token = data.token || '';
_this.$store.state.phone = localStorage.phone = data.cellPhone || '';
var routername = _this.$route.params.routername;
if(routername){//如果是其他页面跳过来登录的。
location.href = '#/'+decodeURIComponent(routername)
}else {

}
}
})
}
}
}
}



